import React, { useState } from "react";
import TypeButton from "./TypeButton";
import { typeToColorMapping } from "../App";

interface TypesSectionProps {
  typesList: string[];
  setTypesList: Function;
  selectType: string;
  setSelectType: Function;
}

const TypesSection: React.FC<TypesSectionProps> = ({
  typesList,
  setTypesList,
  selectType,
  setSelectType,
}) => {
  const [addInput, setAddInput] = useState("");
  const [alert, setAlert] = useState("");
  function handleTypeAdd() {
    if (addInput in typeToColorMapping) {
      if (typesList.includes(addInput.toLowerCase())) {
        setAlert(`<${addInput}> Type Already Added`);
      } else {
        setAlert("");
        setTypesList([addInput.toLowerCase(), ...typesList]);
      }
    } else {
      setAlert(`<${addInput}> Type Does Not Exist`);
    }

    setAddInput("");
  }

  return (
    <div className=" w-full ">
      <div className=" bg-red-600 p-4 border-4 border-black  w-full fixed left-0 top-0 z-10">
        <div className="flex items-center justify-end">
          <button
            onClick={() => {
              window.scrollTo({ top: 0, behavior: "smooth" });
            }}
          >
            <img
              className="w-[150px] overflow-hidden object-cover ml-4"
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/98/International_Pok%C3%A9mon_logo.svg/500px-International_Pok%C3%A9mon_logo.svg.png"
              alt="pokemon"
            />
          </button>
        </div>
        <div className="flex gap-4 items-center"></div>
      </div>
      <div className="  mt-24 p-4 px-4 bg-gray-200 flex gap-2 flex-wrap">
        <div className="flex font-semibold rounded-lg border-4 border-solid border-black  ">
          <input
            type="text"
            placeholder="add type..."
            className="w-24 h-auto px-2 "
            value={addInput}
            onChange={(e) => setAddInput(e.target.value)}
          />
          <button
            className="p-2 px-4  bg-red-500  hover:bg-red-700"
            onClick={handleTypeAdd}
          >
            <p className="text-white font-semibold text-xl"></p>
          </button>
        </div>
        {typesList.map((type, index) => (
          <TypeButton
            setSelectType={setSelectType}
            key={index}
            isLabel={false}
            isSelected={type == selectType}
            type={type}
          />
        ))}
      </div>
      {alert != "" && (
        <div
          className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative"
          role="alert"
        >
          <strong className="font-bold">{alert}</strong>

          <span
            className="absolute top-0 bottom-0 right-0 px-4 py-3"
            onClick={() => setAlert("")}
          >
            <svg
              className="fill-current h-6 w-6 text-red-500"
              role="button"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
            >
              <title>Close</title>
              <path d="M14.348 14.849a1.2 1.2 0 0 1-1.697 0L10 11.819l-2.651 3.029a1.2 1.2 0 1 1-1.697-1.697l2.758-3.15-2.759-3.152a1.2 1.2 0 1 1 1.697-1.697L10 8.183l2.651-3.031a1.2 1.2 0 1 1 1.697 1.697l-2.758 3.152 2.758 3.15a1.2 1.2 0 0 1 0 1.698z" />
            </svg>
          </span>
        </div>
      )}
    </div>
  );
};

export default TypesSection;
